import { Link } from "react-router-dom";
import SearchInput from "./SearchInput";
import ShoppingBasketIcon from '@mui/icons-material/ShoppingBasket';
import StarBorderIcon from '@mui/icons-material/StarBorder';
import './Header.css';

function Header(){
    return(
        <header id="main-header">
            <div className="innerbox">
                <div className="logo-wrap">
                    <img src="./image/logo/logo.png" alt=" " />
                </div>
                <nav>
                    <ul>
                        <li>
                            <Link to='/'></Link>
                        </li>
                        <li>
                            <a href="#"></a>
                        </li>
                        <li>
                            <a href="#"></a>
                            <div className="sub">
                                <ul>
                                    <li>sub3-1</li>
                                    <li>sub3-1</li>
                                    <li>sub3-1</li>
                                    <li>sub3-1</li>
                                </ul>
                            </div>
                        </li>
                        <li>
                            <a href="#"></a>
                            <div className="sub">
                                <ul>
                                    <li>sub4-1</li>
                                    <li>sub4-1</li>
                                    <li>sub4-1</li>
                                    <li>sub4-1</li>
                                </ul>
                            </div>
                        </li>
                        <li>
                            <a href="#"></a>
                        </li>
                    </ul>
                </nav>
                <SearchInput />
                <div className="icon-wrap">
                    <div className="cart">
                        <Link to='/cart'>
                            <ShoppingBasketIcon/>
                        </Link>
                    </div>
                    <div className="user">
                        <Link to='/wishlist'>
                            <StarBorderIcon/>
                        </Link>
                    </div>
                </div>
            </div>
        </header>
    )
}

export default Header;