import { Swiper, SwiperSlide } from 'swiper/react';
import { EffectFade, Navigation, Pagination } from 'swiper/modules';

import 'swiper/css';
import 'swiper/css/effect-fade';
import 'swiper/css/pagination';
function Banner(){
    return(
        
        <div id="banner">
            <Swiper
                spaceBetween={30}
                effect={'fade'}
                navigation={true}
                pagination={{
                clickable: true,
                }}
                modules={[EffectFade, Navigation, Pagination]}
                className="mySwiper"
            >
                <SwiperSlide>
                    <img src="./image/banner/banner1.jpg" alt="1" />
                </SwiperSlide>
                <SwiperSlide>
                    <img src="./image/banner/banner2.jpg" alt="2" />
                </SwiperSlide>
                <SwiperSlide>
                    <img src="./image/banner/banner3.jpg" alt="3" />
                </SwiperSlide>
            </Swiper>
        </div>
    )
}

export default Banner;