import { useDispatch, useSelector } from 'react-redux';
import { addToCart } from '../redux/slices/cartSlice';
import { useParams } from 'react-router-dom';
import { toggleWishlist } from '../redux/slices/wishlistSlice';
import { fetchProducts } from '../redux/slices/productsSlice';
import { useEffect } from 'react';
import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import './ProductDetail.css'

function ProductsDetail() {
  const { id } = useParams();
  const dispatch = useDispatch();
  const isInWishlist = (id) => wishlistItems.some(item => item.id === id);
  const {list, status } = useSelector(state => state.products);
  const wishlistItems = useSelector(state => state.wishlist.items);

  useEffect(() => {
    if (list.length === 0) {
      dispatch(fetchProducts());
    }
  }, [dispatch, list.length]);

  const product = list.find(item => item.id === parseInt(id));

  if (status === 'loading') return <p>   ...</p>;
  if (!product) return <p>   .</p>;

  return (
    <div id='product-detail'>
      <div className="inner-box">
        <div className="img-wrap">
          <img src={product.src} alt=' '/>
        </div>
        <div className="text-wrap">
          <h2>{product.name}</h2>
          <table>
            <tbody>
              <tr>
                <th>:</th>
                <td>{product.price.toLocaleString()}</td>
              </tr>
              <tr>
                <th>:</th>
                <td>{product.category}</td>
              </tr>
              <tr>
                <th>:</th>
                <td>{product.brand}</td>
              </tr>
              <tr>
                <th>:</th>
                <td>{product.reviewCount}</td>
              </tr>
            </tbody>
          </table>
          <button onClick={() => dispatch(addToCart(product))} className='to-cart'>
            <AddShoppingCartIcon />  
          </button>
          <button onClick={() => dispatch(toggleWishlist(product))} className='to-wish'>
            {isInWishlist(product.id) ? <FavoriteIcon /> : <FavoriteBorderIcon/>}
          </button>
        </div>
      </div>
        <img src={product.detail} alt=" " />
    </div>
  );
}

export default ProductsDetail